.custom_bio{
    width: 30%;
    height: 70%;
    display: flex;
    position: fixed;
    border: 2px solid #3577ef;
    border-radius: 30px;
    z-index: 101;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(256, 256, 256, .7);
    justify-content: center;
    font-size: 22px;
}
.background{
    width: 100%;
    height: 100%;
    display: flex;
    position: fixed;
    background: rgba(0, 0, 0, .7);
    z-index: 10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.container{
    width: 65%;
}
.blocks{
    margin-top: 10px;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
    height: 100%;
}
.socials{
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 32px;
}
.edit{
    font-size: 32px;
}

.custom_block{
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.custom_label, .custom_input, .custom_input_oth{
        margin: 10px;
}

.custom_input, .custom_input_oth{
    border: 0;
    border-bottom: 2px solid  #000000;
    background: rgba(256, 256, 256, 0);
    outline: none;
    font-size: 20px;
}
.custom_input_oth{
    width: 48%;
}

.save{
    position: absolute;
    bottom: 30px;
    padding: 8px 30px;
    border: 2px solid  #3577ef;
    border-radius: 10px;
    transition: .2s;
}

.save:hover{
    background-color: #000000;
    border: 2px solid #ffffff;
    color: #ffffff;
}




@media (max-width: 1200px) {
    .custom_bio{
        width: 60%;
        height: 75%;
    }
}


@media (max-width: 800px) {
    .custom_bio{
        width: 70%;
        height: 75%;
    }
}

@media (max-width: 500px) {
    .custom_bio{
        width: 85%;
        height: 75%;
    }
}