.connection_block{
    display: none;
    width: 200px;
    height: 50px;
    background-color: rgb(255, 1, 1);
    color: aliceblue;
    z-index: 100;
    position: fixed;
    right: 50px;
    top: 20px;
    border-radius: 10px;
    padding: 10px;
    
}
.animation{
    display: flex;
    animation-name: connection;
    animation-duration: 5s;
    animation-fill-mode: forwards;
}
.message{
    color: rgb(255, 255, 255);
    z-index: 100;
    font-size: 18px;
}

@keyframes connection {
    0%{
        top: -200px;
        display: none;
        opacity: 0;

    }
    25%{
        top: 20px;
        display: flex;
        opacity: 1;
        
    }
    80%{
        top: 20px;
        display: flex;
        opacity: 1;
    }
    100%{
        top: -200px;
        display: none;
        opacity: 0;

    }
}